import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button'
import Collapse from '@material-ui/core/Collapse'
import Divider from '@material-ui/core/Divider'
import Drawer from '@material-ui/core/Drawer'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import DistributionCenterIcon from '@material-ui/icons/Business'
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import HomeIcon from '@material-ui/icons/Home'
import SupplierIcon from '@material-ui/icons/LocalGroceryStore'
import FleetIcon from '@material-ui/icons/LocalShipping'
import MenuIcon from '@material-ui/icons/Menu'
import OpenIcon from '@material-ui/icons/OpenInNew'
import PersonIcon from '@material-ui/icons/Person'
import StoreIcon from '@material-ui/icons/Store'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { bindActionCreators } from 'redux'

import config from '../../config'
import { logout } from '../auth/AuthActions'

const drawerWidth = 250

const styles = theme => ({
  logo: {
    width: 110,
  },
  grow: {
    flexGrow: 1,
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  btnLogout: {
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
    backgroundColor: theme.palette.primary.main,
    boxShadow:
      '0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  listDrawer: {
    paddingTop: 0,
    zoom: 0.99,
  },
})

class Menu extends Component {
  state = { open: false }

  handleClick = () => {
    this.setState(state => ({ open: !state.open }))
  }

  render() {
    const {
      classes,
      theme,
      open,
      handleDrawerOpen,
      handleDrawerClose,
    } = this.props

    return (
      <div>
        <AppBar
          position="fixed"
          className={classNames(classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar disableGutters={!open}>
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={handleDrawerOpen}
              className={classNames(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <div className={classes.grow}>
              {/* <img className={classes.logo} src="/img/logo.png" /> */}
              <Typography variant="h6" color="inherit" noWrap>
                {config.title}
              </Typography>
            </div>
            <Button
              className={classes.btnLogout}
              onClick={this.props.logout}
              color="inherit"
            >
              Sair
            </Button>
          </Toolbar>
        </AppBar>

        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton light onClick={handleDrawerClose}>
              <ChevronLeftIcon color="secondary" />
            </IconButton>
          </div>
          <Divider />
          <List className={classes.listDrawer}>
            <ListItem button
              component={Link}
              to="/"
              onClick={this.props.handleDrawerClose}>
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText inset
                primary="Home" />
            </ListItem>
            <ListItem button onClick={this.handleClick}>
              <ListItemIcon>
                <OpenIcon />
              </ListItemIcon>
              <ListItemText inset primary="Cadastros Básicos" />
              {this.state.open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={this.state.open} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItem button
                  component={Link}
                  to="/users"
                  onClick={this.props.handleDrawerClose}>
                  <ListItemIcon>
                    <PersonIcon />
                  </ListItemIcon>
                  <ListItemText inset
                    primary="Usuários" />
                </ListItem>
                <ListItem button
                  component={Link}
                  to="/stores"
                  onClick={this.props.handleDrawerClose}>
                  <ListItemIcon>
                    <StoreIcon />
                  </ListItemIcon>
                  <ListItemText inset
                    primary="Lojistas" />
                </ListItem>
                <ListItem button
                  component={Link}
                  to="/suppliers"
                  onClick={this.props.handleDrawerClose}>
                  <ListItemIcon>
                    <SupplierIcon />
                  </ListItemIcon>
                  <ListItemText inset
                    primary="Fornecedores" />
                </ListItem>
                <ListItem button
                  component={Link}
                  to="/distributioncenters"
                  onClick={this.props.handleDrawerClose}>
                  <ListItemIcon>
                    <DistributionCenterIcon />
                  </ListItemIcon>
                  <ListItemText inset
                    primary="Centros de Distribuição" />
                </ListItem>
                <ListItem button
                  component={Link}
                  to="/fleet"
                  onClick={this.props.handleDrawerClose}>
                  <ListItemIcon>
                    <FleetIcon />
                  </ListItemIcon>
                  <ListItemText inset
                    primary="Frotistas" />
                </ListItem>
              </List>
            </Collapse>
          </List>
        </Drawer>
      </div>
    )
  }
}

Menu.propTypes = {
  classes: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({ user: state.auth.user || [] })

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      logout,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Menu))
